@import url(https://fonts.googleapis.com/css?family=Dosis&display=swap);

img {
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  /* min-height: 100vh; */
  background: #000;
  color: #fff;
  position: relative;
  font-family: 'Dosis', sans-serif;
}
header {
  display: grid;
  grid-template-columns: 1fr 1fr 2fr 1fr;
  height: 5rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background: #000;
  width: 100%;
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.5);
}

@media (max-width:768px) {
  header {
    grid-template-columns: 1fr 2fr .5fr;
  }
}

.header-logo {
  border: .1px solid #fff;
  border-top: none;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  padding: 0 .5rem;
  border-left: none;
}

.header-rights {
  border: .1px solid #fff;
  border-top: none;
  border-left: none;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  padding: 0 .5rem;
}

@media (max-width:768px) {
  .header-rights {
    display: none;
  }
}

.search {
  display: flex;
  align-items: center;

  height: 100%;
  padding: 0 .5rem;
  border: .1px solid #fff;
  border-left: none;
  border-top: none;
}

.search input {
  height: 2rem;
  font-size: 1rem;
  background: none;
  outline: none;
  border: none;
  width: 80%;
  border-bottom: .1px solid #fff;
  color: #ccc;

}

.search-icon i {
  cursor: pointer;
  font-size: 1.5rem;
  color: #ccc;

}

.search-icon {
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;

}

.search i:hover {
  color: #fff;
}

.goto-home {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 .5rem;
  border-bottom: .1px solid #fff;
  justify-content: center;

}

.goto-home i {
  font-size: 1.5rem;
  color: #ccc;
  cursor: pointer;

}

.goto-home i:hover {
  color: #fff;
}
footer {
  position: fixed;
  bottom: 0;
  height: 3.5rem;
  left: 0;
  display: grid;
  grid-template-columns: auto auto;
  width: 100%;
  background: #000;
  z-index:100;
  box-shadow: 0 -5px 8px rgba(0, 0, 0, 0.5);
}

footer>div {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 .5rem .5rem .5rem;
}

footer>div:nth-child(1) {
  border: .1px solid #fff;
  border-left: 0;
  border-bottom: 0;

}

footer>div:nth-child(2) {
  border: .1px solid #fff;
  border-right: none;
  border-left: none;
  border-bottom: none;
}

.footer-right span:not(last-of-type) {
  margin-right: 1rem;
}

@media (max-width:768px) {
  .footer-right span:not(:last-of-type) {
    margin-right: .5rem;
  }
}
nav ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;

}

nav {
  margin: 5rem 0 3.5rem 0;
}

nav ul li {
  /* padding: .5rem 1.5rem; */
  position: relative;

}

nav ul .link::before {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: -1;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: all .5s ease-out;
  transition: all .5s ease-out;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  position: absolute;

}

nav ul .link:hover:before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.link {
  color: #fff;
  font-size: 2rem;
  width: 100%;
  height: 100%;
  padding: 1rem 2rem;
  display: inline-block;
  position: relative;
  text-decoration: none;
  -webkit-transition: all .5s ease-out;
  transition: all .5s ease-out;
  border: .1px solid #fff;
}

@media (max-width:768px) {
  .link {
    padding: .8rem 1.7rem;
  }
}

@media (max-width:576px) {
  .link {
    padding: .5rem 1.5rem;
  }
}

nav ul li .link:hover {
  color: #000;
}


